@import url("https://fonts.googleapis.com/css2?family=Abel&family=Roboto:wght@100;400&display=swap");

html {
  scroll-behavior: smooth;
}

body {
  width: 100%;
  background-color: #15161b;
  color: white;
  font-family: "Abel", sans-serif;
  background-image: url(assets/7971.jpg);
  background-size: cover;
  /* background-repeat: no-repeat; */
}
.container {
  display: grid;
  grid-template-rows: 60px, 1fr, 1fr, 1fr, 1fr;
}
.header {
  /* justify-content: center; */
  height: 40px;
  display: flex;
  justify-content: space-between;
  font-size: 18pt;
  grid-row: 1;
  position: fixed;
  width: 100%;
}
.logo {
  /* display: inline; */
  margin-top: 10px;
  margin-left: 100px;
}
.navbar {
  margin-right: 100px;
  /* margin-top: 10px; */
  /* margin-bottom: 0;
    padding-bottom: 0; */
  font-size: 17px;
}

.header li {
  display: inline;
  margin-right: 20px;
  margin-top: 0px;
}

.header a {
  text-decoration: none;
  color: white;
}
.header a:hover {
  text-shadow: 0 0 5px white, 0 0 10px white, 0 0 15px white;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

.main {
  grid-row: 2;
  height: 450px;
  /* background-color: darkslategrey; */
  font-family: "Roboto", sans-serif;
  font-size: 18pt;
  /* text-align: right; */
  font-weight: 100;
  margin-top: 250px;
  display: flex;
  justify-content: center;
  width: 100%;
  column-gap: 20px;
  /* animation: spin 3s infinite linear; */
}
.main p {
  display: inline;
  /* text-align: right; */
}
.name {
  font-weight: 400;
  font-size: 24pt;
}
.status {
  font-weight: 900;
  font-size: 30pt;
  opacity: 0.6;
  text-align: right;
  line-height: 95%;
}
.greating {
  text-align: left;
  margin-top: 50px;
}
.projectContainer {
  grid-row: 3;
  padding-top: 50px;
  display: grid;
  grid-template-rows: 1fr, 1fr;
  place-items: center;
  /* height: 500px; */
}
.projectContainer h2 {
  grid-row: 1;
}
.imgContainer {
  grid-row: 2;
  height: 400px;
  display: grid;
  grid-template-columns: 1fr, 1fr;
  gap: 50px;
  padding-top: 16px;
}

.imgContainer img {
  width: 450px;
  /* display: inline; */
}
.image1 {
  grid-column: 1;
}
.image2 {
  grid-column: 2;
}

.aboutContainer {
  grid-row: 4;
  /* padding-left: 50px; */
  padding-top: 50px;
  display: grid;
  place-items: center;
}
.aboutContainer p {
  width: 450px;
  font-family: "Roboto", sans-serif;
  grid-row: 2;
  height: 350px;
}

.contactContainer {
  grid-row: 5;
  /* padding-left: 50px; */
  padding-top: 50px;
  display: grid;
  place-items: center;
}

.form {
  width: 500px;
  margin-bottom: 30px;
  grid-row: 2;
}

.contactContainer button {
  grid-row: 3;
  margin-bottom: 100px;
}
